import {
  Pagination,
  Table,
  TableCell,
  TableContainer,
  TableFooter,
  TableHeader,
  WindmillContext,
} from "@windmill/react-ui";
import LineChart from "components/chart/LineChart/LineChart";
import PieChart from "components/chart/Pie/PieChart";
import CardItem from "components/dashboard/CardItem";
import CardItemTwo from "components/dashboard/CardItemTwo";
import ChartCard from "components/chart/ChartCard";
import OrderTable from "components/dashboard/DashbaordReview";
import BookingTable from "components/dashboard/DashboardBooking";
import ExpenseDashboard from "components/dashboard/TotalExpenseDashboard"
import IncomeTable from "components/dashboard/IncomeDashboard";
import TableLoading from "components/preloader/TableLoading";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import * as dayjs from "dayjs";
import isBetween from "dayjs/plugin/isBetween";
import isToday from "dayjs/plugin/isToday";
import isYesterday from "dayjs/plugin/isYesterday";
import useAsync from "hooks/useAsync";
import useFilter from "hooks/useFilter";
import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FiCheck, FiClock, FiRefreshCw, FiShoppingCart, FiTruck, FiUser } from "react-icons/fi";
import { ImCreditCard, ImStack } from "react-icons/im";
import OrderServices from "services/OrderServices";
import { getAllDashboardData } from "services/Api";
import { FiFlag } from "react-icons/fi";
import { FiLayers } from "react-icons/fi";
import { FiMoreVertical } from "react-icons/fi";
import { FiMonitor } from "react-icons/fi";
import { ImMusic } from "react-icons/im";
import { ImDroplet } from "react-icons/im";
import VegCount from "assets/img/veh-count.png";
import TotalMonthlyCount from "assets/img/Total-monthly-Booking.png";
import TodayCheckout from "assets/img/Today-Check-out.png";
import NonVegCount from "assets/img/Nonveg-Count.png";
import TotalBookings from "assets/img/Total-Bookings.png"
import TodayBooking from "assets/img/Today-Booking.png"
import TodayCheckin from "assets/img/Today-Check-in.png"



const Dashboard = () => {
  const { globalSetting } = useFilter();
  const { mode } = useContext(WindmillContext);
  const [loading, setLoading]= useState(false)
  const [ComplateDashboardData, setComplateDashboardData] = useState([])
  dayjs.extend(isBetween);
  dayjs.extend(isToday);
  dayjs.extend(isYesterday);

  useEffect(() => {
    getDashBoardDetails()
  }, []);
  const getDashBoardDetails = async () => {
    const dashboarddata = await getAllDashboardData();
    console.log(dashboarddata.data?.data,"dashboarddata")
    setComplateDashboardData(dashboarddata.data?.data)
    setLoading(false)
    setchartdata(dashboarddata.data?.data?.propertyCount)
  };
  const { currentPage, handleChangePage, lang } = useContext(SidebarContext);
  const [salesReport, setSalesReport] = useState([ {
    date: "2023-08-01",
    total: 1200,
    order: 20,
  },
  {
    date: "2023-08-02",
    total: 1400,
    order: 18,
  },
  {
    date: "2023-08-03",
    total: 1600,
    order: 25,
  },
  {
    date: "2023-08-04",
    total: 1300,
    order: 22,
  },
  {
    date: "2023-08-05",
    total: 1500,
    order: 24,
  },]);
  const [chartdata, setchartdata] = useState ([])
  const { data: bestSellerProductChart, loading: loadingBestSellerProduct } =[]
  const { data: dashboardRecentOrder, loading: loadingRecentOrder } = useAsync(
    () => OrderServices.getDashboardRecentOrder({ page: currentPage, limit: 8 })
  );
  const { data: dashboardOrderCount, loading: loadingOrderCount } = useAsync(
    OrderServices.getDashboardCount
  );
  const { data: dashboardOrderAmount, loading: loadingOrderAmount } = useAsync(
    OrderServices.getDashboardAmount
  );
  const currency = globalSetting?.default_currency || "₹";
  const { dataTable, serviceData } = useFilter(dashboardRecentOrder?.orders);
  const { t } = useTranslation();
  const iconsq = [FiShoppingCart, FiClock, FiUser,FiTruck,FiRefreshCw, FiFlag, FiLayers,FiMoreVertical, FiMonitor/* Add more icons as needed */];

  return (
    <>
      <PageTitle>{t("DashboardOverview")}</PageTitle>

      <div className="grid gap-4 mb-8 md:grid-cols-4 xl:grid-cols-4">
        <CardItemTwo
          mode={mode}
          title="Weekly Booking"
          title2="Weekly Booking"
          Icon={TodayCheckin}
          price={ComplateDashboardData?.totalWeeklyBookingCount}
          className="text-white dark:text-green-100 bg-teal-500"
        />

        <CardItemTwo
          mode={mode}
          title="Today Booking"
          title2="Today Booking"
          Icon={TodayBooking}
          price={ComplateDashboardData?.todayBooking}
          className="text-white dark:text-orange-100 bg-orange-400"
          loading={loadingOrderAmount}
        />

        <CardItemTwo
          mode={mode}
          title2="Todays Check-in"
          Icon={TodayCheckin}
          price={ComplateDashboardData?.todayCheckInGuestCount}
          className="text-white dark:text-green-100 bg-blue-500"
          loading={loadingOrderAmount}
        />
            <CardItemTwo
          mode={mode}
          title2="Todays Check-Out"
          Icon={TodayCheckout}
          price={ComplateDashboardData?.todayCheckOutGuestCount}
          className="text-white dark:text-green-100 bg-yellow-500"
          loading={loadingOrderAmount}
        />

        <CardItemTwo
          mode={mode}
          title2="Total Monthly Booking"
          Icon={TotalMonthlyCount}
          price={ComplateDashboardData?.totalMonthlyBookingCount}
          className="text-white dark:text-green-100 bg-green-500"
          loading={loadingOrderAmount}
        />
           <CardItemTwo
          mode={mode}
          title2="Total Booking"
          Icon={TotalBookings}
          price={ComplateDashboardData?.totalBookingCount}
          className="text-white dark:text-green-100 bg-pink-500"
          loading={loadingOrderAmount}
        />
          <CardItemTwo
          mode={mode}
          title2="Veg Count"
          Icon={VegCount}
          price={ComplateDashboardData?.totalVegetariansCount}
          className="text-white dark:text-green-100 bg-green-veg"
          loading={loadingOrderAmount}
        />
        
          <CardItemTwo
          mode={mode}
          title2="Non Veg Count"
          Icon={NonVegCount}
          price={ComplateDashboardData?.totalNonVegetariansCount}
          className="text-white dark:text-green-100 bg-red-nonveg"
          loading={loadingOrderAmount}
        />
      </div>
      <PageTitle>{t("Stay Rooms")}</PageTitle>
      <div className="grid gap-4 md:grid-cols-2 xl:grid-cols-4">
      {ComplateDashboardData?.stayPackageWithSlot?.map((stay,i) => (
         <CardItem
          title={stay?.name}
          // Icon={ImMusic}
          Icon={iconsq[i % iconsq.length]} 
          loading={loadingOrderCount}
          quantity={`Total Slots : ${stay?.slots_availability}`}
          quantity1={`Slots Availability : ${stay?.remaining_slots}`}
          className="text-orange-600 dark:text-orange-100 bg-orange-100 dark:bg-orange-500"
        />
      ))}
      </div>

      <PageTitle>{t("Day Out Packages")}</PageTitle>
      <div className="grid gap-4 md:grid-cols-2 xl:grid-cols-4">
      {ComplateDashboardData?.dayoutPackageWithSlot?.map((stay,i) => (
         <CardItem
          title={stay?.name}
          Icon={iconsq[i % iconsq.length]} 
          loading={loadingOrderCount}
          quantity={`Total Slots : ${stay?.slots_availability}`}
          quantity1={`Slots Availability : ${stay?.remaining_slots}`}
          className="text-orange-600 dark:text-orange-100 bg-orange-100 dark:bg-orange-500"
        />
      ))}
      </div>

      <PageTitle>{t("Events Packages")}</PageTitle>
      <div className="grid gap-4 md:grid-cols-2 xl:grid-cols-4">
      {ComplateDashboardData?.event_detailsPackageWithSlot?.map((stay,i) => (
         <CardItem
          title={stay?.name}
          Icon={iconsq[i % iconsq.length]} 
          loading={loadingOrderCount}
          quantity={`Total Slots : ${stay?.slots_availability}`}
          quantity1={`Slots Availability : ${stay?.remaining_slots}`}
          className="text-orange-600 dark:text-orange-100 bg-orange-100 dark:bg-orange-500"
        />
      ))}

      
      </div>

      <div className="grid gap-4 md:grid-cols-2 my-8">
        <ChartCard
          mode={mode}
          loading={loadingOrderAmount}
          title={t("Accounts")}
        >
          <LineChart salesReport={salesReport} />
        </ChartCard>

        <ChartCard
          mode={mode}
          loading={loadingBestSellerProduct}
          title={t("Best Weekly Packages")}
        >
          <PieChart data={chartdata} />
        </ChartCard>
      </div>


      <div className="grid gap-4 md:grid-cols-2">
        <div>
      <PageTitle>{t("Latest Customer Reviews")}</PageTitle>
      {loadingRecentOrder ? (
        <TableLoading row={5} col={4} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8">
          <Table>
            <TableHeader>
              <tr>
              <TableCell>{t("S.no")}</TableCell>
              <TableCell>{t("Property Name")}</TableCell>
                <TableCell>{t("User Name")}</TableCell>
                <TableCell>{t("Ratings")}</TableCell>
                <TableCell className="">{t("Status")}</TableCell>
              </tr>
            </TableHeader>

            <OrderTable
              products={ComplateDashboardData?.LatestCustomerReviews}
            />
          </Table>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no orders right now." />
      )}
      </div>
      <div>
      <PageTitle>{t("Latest Booking")}</PageTitle>
      {loadingRecentOrder ? (
        <TableLoading row={5} col={4} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8">
          <Table>
            <TableHeader>
              <tr>
              <TableCell>{t("S.no")}</TableCell>
              <TableCell>{t("Name")}</TableCell>
              <TableCell>{t("Property Name")}</TableCell>
              <TableCell>{t("package Name")}</TableCell>
                <TableCell>{'Date'}</TableCell>
              </tr>
            </TableHeader>

            <BookingTable
              products={ComplateDashboardData?.NewBookings}
            />
          </Table>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no orders right now." />
      )}
      </div>
      </div>

      <div className="grid gap-4 md:grid-cols-2">
        <div>
      <PageTitle>{t("Total Expense")}</PageTitle>
      {loadingRecentOrder ? (
        <TableLoading row={5} col={4} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8">
          <Table>
            <TableHeader>
              <tr>
              <TableCell>{t("S.no")}</TableCell>
              <TableCell>{t("Property Name")}</TableCell>
              {/* <TableCell>{t("Vendor Name")}</TableCell> */}
                <TableCell>{'Expenses'}</TableCell>
              </tr>
            </TableHeader>

            <ExpenseDashboard
              products={ComplateDashboardData?.TotalExpenseStats}
            />
          </Table>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no orders right now." />
      )}
      </div>
      <div>
      <PageTitle>{t("Total Income")}</PageTitle>
      {loadingRecentOrder ? (
        <TableLoading row={5} col={4} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8">
          <Table>
            <TableHeader>
              <tr>
              <TableCell>{t("S.no")}</TableCell>
              <TableCell>{t("Property Name")}</TableCell>
              <TableCell>{t("Income")}</TableCell>
              {/* <TableCell>{t("Last Updated")}</TableCell> */}
              </tr>
            </TableHeader>

            <IncomeTable
              products={ComplateDashboardData?.TotalIncomStats}
            />
          </Table>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no orders right now." />
      )}
      </div>
      </div>
    </>
  );
};

export default Dashboard;
