import {
    Select,
    TableBody,
    TableCell,
    TableRow,
  } from "@windmill/react-ui";
  import DeleteModal from "components/Properties/DeleteProperty";
  import ShowHideButton from "components/table/ShowHideButton";
  import Tooltip from "components/tooltip/Tooltip";
  import { t } from "i18next";
  import { useContext, useState } from "react";
  import { FiEdit, FiTrash2 } from "react-icons/fi";
  import { SidebarContext } from "context/SidebarContext";
  import MainDrawer from "components/Properties/EditPropertiesModal";
  import ProductDrawer from "components/Review/AddReviewApproval";
import LabelArea from "components/form/LabelArea";
import { addapprovestausforReview } from "services/Api";
import { notifySuccess } from "utils/toast";
import moment from "moment";
  
  
  const ProductTable = ({products,getPropertyList,property,getReviewList}) => {
    const {toggleModal ,toggleDrawer1,toggleDrawer,setIsDrawerOpen1} =
    useContext(SidebarContext);

       const handleStatusChange= async (selectedValue,properties) => {
            console.log("Selected Value:", selectedValue,properties);
            let payload = {
                "id":properties?.id,
                "approval_status":selectedValue
            }
            const res = await addapprovestausforReview(payload);
            if (res.data.status == true) {
              notifySuccess(res.data.message);
              console.log(res,"dsfs")
                getReviewList()
            }
          }
    return (
      <>
        <MainDrawer>
        <ProductDrawer isOpen={toggleDrawer1} getPropertyList={getPropertyList} onClose={toggleDrawer1}  property={property}/>
        </MainDrawer> 
        <div>
    </div>
        <TableBody>
        {products?.slice(0, 5).map((properties, i) => (
            <TableRow key={i + 1}>
               <TableCell>
              <span className="text-sm">{i+1}</span>
              </TableCell>           
             <TableCell className="">
                <span className="text-sm ">{properties?.User?.name}</span>
              </TableCell>
              <TableCell className="">
              <span className="text-sm">{properties?.Booking_property?.name}</span> 
              </TableCell>
              <TableCell className="">
              <span className="text-sm">{properties?.Booking_package?.name}</span> 
              </TableCell>
              <TableCell className="">
              <span className="text-sm">{moment(properties?.check_in_date).format("DD-MM-YYYY")}</span><br/>
              {properties?.check_in_time == null ? "": <span className="text-sm">{moment(properties?.check_in_time).format("HH:mm")}</span>}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </>
    );
  };
  
  export default ProductTable;
  
  
  
  