import combinate from "combinate";
import { useContext, useEffect, useRef, useState } from "react";
import { useForm } from "react-hook-form";
import { useLocation } from "react-router-dom";
import swal from "sweetalert";

import useAsync from "./useAsync";
import { SidebarContext } from "context/SidebarContext";
import { notifyError, notifySuccess } from "utils/toast";
import SettingServices from "services/SettingServices";
import { Updateproperty } from "services/Api";

const useProductSubmit = (id, getPropertyList, property,pagenumber) => {
  const location = useLocation();
  const { isDrawerOpen,toggleDrawer, closeDrawer,closeDrawer1, setIsUpdate, lang } =
    useContext(SidebarContext);

  const { data: globalSetting } = useAsync(SettingServices.getGlobalSetting);
  const resetRef = useRef([]);
  const resetRefTwo = useRef("");
  const [imageUrl, setImageUrl] = useState([]);
  const [tag, setTag] = useState([]);
  const [values, setValues] = useState({});
  let [variants, setVariants] = useState([]);
  const [variant, setVariant] = useState([]);
  const [totalStock, setTotalStock] = useState(0);
  const [quantity, setQuantity] = useState(0);
  const [product, setproduct] = useState([]);
  const [originalPrice, setOriginalPrice] = useState(0);
  const [price, setPrice] = useState(0);
  const [sku, setSku] = useState("");
  const [barcode, setBarcode] = useState("");
  const [isBasicComplete, setIsBasicComplete] = useState(false);
  const [tapValue, setTapValue] = useState("Basic Info");
  const [isCombination, setIsCombination] = useState(false);
  const [attTitle, setAttTitle] = useState([]);
  const [variantTitle, setVariantTitle] = useState([]);
  const [attributes, setAttributes] = useState([]);
  const [productId, setProductId] = useState("");
  const [updatedId, setUpdatedId] = useState(id);
  const [imgId, setImgId] = useState("");
  const [isBulkUpdate, setIsBulkUpdate] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState([]);
  const [defaultCategory, setDefaultCategory] = useState([]);
  const [defaultCategory1, setDefaultCategory1] = useState([]);
  const [resaddess, setresaddess] = useState({});
  const [resData, setResData] = useState({});
  const [language, setLanguage] = useState(lang);
  const [openModal, setOpenModal] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [longDescription, setlongDescription] = useState([])
  const [SpecialDescription, setSpecialDescription] = useState([]);
  const [slug, setSlug] = useState("");
  // handle click
  // const onCloseModal = () => setOpenModal(false);
  const onCloseModal = () => {
    console.log("eff","dsf")
    clearErrors([]);
    setOpenModal(false);
    reset()
    
  }
  const {
    register,
    handleSubmit,
    setValue,
    reset,
    clearErrors,
    formState: { errors },
  } = useForm();

  const onSubmit = async (data) => {
    try {
      if (imageUrl.length === 0) {
        setTimeout(() => {
          setIsSubmitting(false)
          }, 2000);
        return notifyError("File is empty. Please select a valid file.");
      } else {
    setIsSubmitting(true)
    const propertyImages = imageUrl.map((item) => ({
      image: typeof item.image === "string" ? item.image : item.image.image,
    }));
    const finalimages = propertyImages.map((item) => {
      const { id, ...rest } = item;
      return rest;
    });
    console.log(imageUrl, "imageUrl,imageUrl1");
    console.log(
      "data is data edit",
      property,
      data,
      imageUrl,
      defaultCategory1,
      defaultCategory,
      resaddess,
      propertyImages
    );
    let payload = {
      location_id: data.PropertyId ? data.PropertyId : property?.PropertyId,
      name: data.title ? data.title : property?.name,
      address: data.resaddess ? data.resaddess : property?.address,
      id: property?.id,
      map_url: data?.Map,
      description: longDescription ? longDescription : property?.description,
      Safety_and_hygiene_id: property?.Property_safeties
        ? data.Safety_and_hygiene_id
        : data.Safety_and_hygiene_id,
      property_images: propertyImages,
      "special_instruction":data?.SpecialInstruction ? data?.SpecialInstruction: property?.special_instruction ,
      "payment_gateway_id":data?.payment ? data?.payment :property?.payment_gateway_id,
      special_description:SpecialDescription

    };
setTimeout(() => {
  setIsSubmitting(false)
}, 2000);
    console.log(payload, "payload", "property---", property);
    const res = await Updateproperty(payload);
    if (res.data.status == true) {
      console.log(res, "dsfs");
      handleClearVariant();
      notifySuccess(res.data.message);
  setIsSubmitting(false)
  closeDrawer1();
      getPropertyList(pagenumber);
    }}
  } catch (err) {
    notifyError(err ? err?.response?.data?.message : err.message);
    setIsSubmitting(false);
  }
}
  //generate all combination combination
  const handleGenerateCombination = () => {
    if (Object.keys(values).length === 0) {
      return notifyError("Please select a variant first!");
    }

    const result = variants.filter(
      ({
        originalPrice,
        discount,
        price,
        quantity,
        barcode,
        sku,
        productId,
        image,
        ...rest
      }) => JSON.stringify({ ...rest }) !== "{}"
    );

    // console.log("result", result);

    setVariants(result);

    const combo = combinate(values);

    combo.map((com, i) => {
      if (JSON.stringify(variant).includes(JSON.stringify(com))) {
        return setVariant((pre) => [...pre, com]);
      } else {
        const newCom = {
          ...com,

          originalPrice: originalPrice || 0,
          price: price || 0,
          quantity: Number(quantity),
          discount: Number(originalPrice - price),
          productId: productId && productId + "-" + (variants.length + i),
          barcode: barcode,
          sku: sku,
          image: imageUrl[0] || "",
        };

        setVariants((pre) => [...pre, newCom]);
        return setVariant((pre) => [...pre, com]);
      }
    });

    setValues({});

    // resetRef?.current?.map((v, i) =>
    //   resetRef?.current[i]?.resetSelectedValues()
    // );
  };
  useEffect(() => {
    if (!isDrawerOpen) {
      setImageUrl("");
      clearErrors("title");
      clearErrors("Map");
      clearErrors("PropertyId");
      clearErrors("phone");
      clearErrors("role");
      clearErrors("joiningDate");
      return;
    }
  }, [id, setValue, isDrawerOpen.email, clearErrors]);
  const handleClearVariant = () => {
    setDefaultCategory1([]);
    setDefaultCategory([]);
    setImageUrl([]);
    setresaddess({});
    setValue("resaddess", "");
    setValue("title", "");
    setValue("Description", "");
  };

  //for edit combination values
  const handleEditVariant = (variant) => {
    setTapValue("Combine");
  };

  //for remove combination values
  const handleRemoveVariant = (vari, ext) => {
    // console.log("handleRemoveVariant", vari, ext);
    swal({
      title: `Are you sure to delete this ${ext ? "Extra" : "combination"}!`,
      text: `(If Okay, It will be delete this ${
        ext ? "Extra" : "combination"
      })`,
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        const result = variants.filter((v) => v !== vari);
        setVariants(result);
        // console.log("result", result);
        const {
          originalPrice,
          price,
          discount,
          quantity,
          barcode,
          sku,
          productId,
          image,
          ...rest
        } = vari;
        const res = variant.filter(
          (obj) => JSON.stringify(obj) !== JSON.stringify(rest)
        );
        setVariant(res);
        setIsBulkUpdate(true);
        // setTimeout(() => setIsBulkUpdate(false), 500);
        const timeOutId = setTimeout(() => setIsBulkUpdate(false), 500);
        return clearTimeout(timeOutId);
      }
    });
  };

  // handle notification for combination and extras
  const handleIsCombination = () => {
    if ((isCombination && variantTitle.length) > 0) {
      swal({
        title: "Are you sure to remove combination from this product!",
        text: "(It will be delete all your combination and extras)",
        icon: "warning",
        buttons: true,
        dangerMode: true,
      }).then((value) => {
        // console.log(value);
        if (value) {
          setIsCombination(!isCombination);
          setTapValue("Basic Info");
          setVariants([]);
          setVariant([]);
        }
      });
    } else {
      setIsCombination(!isCombination);
      setTapValue("Basic Info");
    }
  };

  //for select bulk action images
  const handleSelectImage = (img) => {
    if (openModal) {
      variants[imgId].image = img;
      setOpenModal(false);
    }
  };

  //for select individual combination image
  const handleSelectInlineImage = (id) => {
    setImgId(id);
    setOpenModal(!openModal);
  };

  //this for variant/combination list
  const handleSkuBarcode = (value, name, id) => {
    variants[id][name] = value;
  };

  const handleProductTap = (e, value, name) => {
    // console.log(e);

    if (value) {
      if (!value)
        return notifyError(
          `${"Please save product before adding combinations!"}`
        );
    } else {
      if (!isBasicComplete)
        return notifyError(
          `${"Please save product before adding combinations!"}`
        );
    }
    setTapValue(e);
  };

  //this one for combination list
  const handleQuantityPrice = (value, name, id, variant) => {
    // console.log("handleQuantityPrice", name, "value", value);
    if (name === "price" && Number(variant.originalPrice) < Number(value)) {
      // variants[id][name] = Number(variant.originalPrice);
      notifyError("SalePrice must be less then or equal of product price!");
      setValue("price", variant.originalPrice);
      setIsBulkUpdate(true);
      const timeOutId = setTimeout(() => setIsBulkUpdate(false), 100);
      return () => clearTimeout(timeOutId);
    }
    setVariants((pre) =>
      pre.map((com, i) => {
        if (i === id) {
          const updatedCom = {
            ...com,
            [name]: Math.round(value),
          };

          return updatedCom;
        }
        return com;
      })
    );

    const totalStock = variants.reduce(
      (pre, acc) => pre + Number(acc.quantity),
      0
    );
    setTotalStock(Number(totalStock));
  };

  //for change language in product drawer
  const handleSelectLanguage = (lang) => {
    setLanguage(lang);
    if (Object.keys(resData).length > 0) {
      setValue("title", resData.title[lang ? lang : "en"]);
      setValue("description", resData.description[lang ? lang : "en"]);
    }
  };

  //for handle product slug
  const handleProductSlug = (value) => {
    setValue("slug", value.toLowerCase().replace(/[^A-Z0-9]+/gi, "-"));
    setSlug(value.toLowerCase().replace(/[^A-Z0-9]+/gi, "-"));
  };

  return {
    tag,
    setTag,
    values,
    language,
    register,
    onSubmit,
    errors,
    slug,
    openModal,
    // attribue,
    setValue,
    setValues,
    variants,
    imageUrl,
    setImageUrl,
    handleSubmit,
    isCombination,
    variantTitle,
    attributes,
    attTitle,
    productId,
    onCloseModal,
    isBulkUpdate,
    globalSetting,
    isSubmitting,
    tapValue,
    setTapValue,
    resetRefTwo,
    handleSkuBarcode,
    handleProductTap,
    selectedCategory,
    setSelectedCategory,
    setDefaultCategory,
    resaddess,
    setresaddess,
    defaultCategory,
    setDefaultCategory1,
    defaultCategory1,
    handleProductSlug,
    handleSelectLanguage,
    handleIsCombination,
    longDescription,
    setlongDescription,
    handleEditVariant,
    handleRemoveVariant,
    SpecialDescription, 
    setSpecialDescription,
    handleClearVariant,
    handleQuantityPrice,
    handleSelectImage,
    handleSelectInlineImage,
    handleGenerateCombination,
  };
};

export default useProductSubmit;
