import { Input, Textarea, Select, Alert } from "@windmill/react-ui";
import React, { useEffect, useState } from "react";
import { Scrollbars } from "react-custom-scrollbars-2";
import { Modal } from "react-responsive-modal";
import "react-responsive-modal/styles.css";
import { useTranslation } from "react-i18next";
import { FiX } from "react-icons/fi";
import useProductSubmit from "hooks/UseProductEdit";
import UploaderThree from "components/image-uploader/UploaderThree";
import Title from "components/form/Title";
import LabelArea from "components/form/LabelArea";
import Error from "components/form/Error";
import Uploader from "components/image-uploader/Uploader";
import InputArea from "components/form/InputArea";
import DrawerButton from "components/form/DrawerButtonEdit";
import { getAllLocationList, getAllSafetyhygineList } from "services/Api";

const ProductDrawer = ({ id, getPropertyList, property, setproperty }) => {
  const [location, setlocation] = useState([]);
  const [SafetyhygieneList, setSafetyhygieneList] = useState([]);
  const [defalutlocation, setdefalutlocation] = useState([]);
  const [option, setoption] = useState([{ name: "Option 1️⃣", id: 1 }]);
  const defaultsefatyValue = property?.Safety_and_hygiene_id;
  const [SelectedsaftydataValues, setSelectedsaftydataValues] =
    useState(defaultsefatyValue);
  const defaultValue = property?.location_id; // Set the default value here
  const [selectedValue, setSelectedValue] = useState(defaultValue);
  const { t } = useTranslation();
  const {
    setValue,
    register,
    onSubmit,
    errors,
    openModal,
    imageUrl,
    setImageUrl,
    handleSubmit,
    onCloseModal,
    isSubmitting,
    tapValue,
    handleProductTap,
    setresaddess,
    handleProductSlug,
    handleSelectLanguage,
    handleSelectImage,
  } = useProductSubmit(id, getPropertyList, property);

  useEffect(() => {
    console.log(property?.Property_safeties, "defalutlocation");
    const arrayResult = Array.from([property?.Property_location]);
    setoption(arrayResult);
    setSelectedValue(property?.location_id);
    setSelectedsaftydataValues(property?.Safety_and_hygiene_id);
    setImageUrl(property?.Property_images);
    console.log(SelectedsaftydataValues, "SelectedsaftydataValues", property);
    getLocationList();
    GetSafetyhygiene();
    setValue("title", property?.name);
    setValue("Map", property?.map_url);
    setValue("PropertyId", property?.Property_location?.id);
    setValue("Description", property?.description);
    setValue("resaddess",property?.address)
    setValue(
      "Safety_and_hygiene",
      property?.Property_safeties?.map((amm) =>
        amm?.Safety_and_hygiene?.id?.toString()
      )
    );
  }, [property]);

  useEffect(() => {
    console.log(defalutlocation, "SelectedsaftydataValues", option, imageUrl);
  }, [imageUrl]);

  const getLocationList = async () => {
    const locationList = await getAllLocationList();
    console.log(locationList, "locationList");
    setlocation(locationList?.data?.data?.propertyLocation);
  };

  const GetSafetyhygiene = async () => {
    const SafetyhygieneList = await getAllSafetyhygineList();
    console.log(SafetyhygieneList, "Safetyhygiene");
    setSafetyhygieneList(SafetyhygieneList?.data?.data?.safteyAndHygiene);
  };
  const handleLocationChange = (event) => {
    setSelectedValue(event.target.value);
  };
  const handlesaftyChange = (event) => {
    const selectedOptions = Array.from(
      event.target.selectedOptions,
      (option) => option.value
    );
    setSelectedsaftydataValues(selectedOptions);
  };

  return (
    <>
      <Modal
        open={openModal}
        onClose={onCloseModal}
        center
        closeIcon={
          <div className="absolute top-0 right-0 text-red-500 focus:outline-none active:outline-none text-xl border-0">
            <FiX className="text-3xl" />
          </div>
        }
      >
        <div className="cursor-pointer">
          <UploaderThree
            imageUrl={imageUrl}
            setImageUrl={setImageUrl}
            handleSelectImage={handleSelectImage}
          />
        </div>
      </Modal>

      <div className="w-full relative p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300">
        {property?.id ? (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Edit Status")}
          />
        ) : (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Edit Status")}
          />
        )}
      </div>

      <Scrollbars className="track-horizontal thumb-horizontal w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)} className="block" id="block">
          {tapValue === "Basic Info" && (
            <div className="px-6 pt-8 flex-grow w-full h-full max-h-full pb-40 md:pb-32 lg:pb-32 xl:pb-32">
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Safety and hygiene ")} />
                <div className="col-span-8 sm:col-span-4">
                  <div className="sm:col-span-3">
                    <Select
                      {...register(`Safety_and_hygiene`, {
                        required: "Select Safety_and_hygiene is required",
                      })}
                      className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                    >
                      <option value="" defaultValue hidden>
                        {t("Select Safety_and_hygiene")}
                      </option>
                      {SafetyhygieneList?.map((ammi, i) => (
                        <option value={ammi.id}>{ammi.name}</option>
                      ))}
                    </Select>
                    <Error errorName={errors.Safety_and_hygiene} pos />
                  </div>
                </div>
              </div>
            </div>
          )}
          <DrawerButton
            id={id}
            save
            title="Product"
            isSubmitting={isSubmitting}
            handleProductTap={handleProductTap}
          />
          <DrawerButton id={id} title="Property" isSubmitting={isSubmitting} />
        </form>
      </Scrollbars>
    </>
  );
};

export default React.memo(ProductDrawer);
